.FAE-QuillWrap {
  .FAE-editable {
    font-family: AvertaStd;
    font-size: 22px;
    display: inline-block;
    margin-left: 5px;

    &-divider {
      display: block;
      height: 25px;
    }

    &.FAE-editable-punctuation {
      margin-left: 0;
      margin-right: 5px;
    }
    &.FAE-editable-spacer {
      margin: 0;
      width: 0;
    }
  }

  .ql-container,
  .ql-editor {
    height: auto;
    padding: 0;
    overflow-x: hidden;
  }
}